import { Employee } from "generated/prisma";
import { ExpandedEmployee, ExpandedEmployeeWithTickets } from "../types/prisma.types";

export function isExpandedEmployeeBase(emp: Employee | ExpandedEmployee): emp is ExpandedEmployee {
  return !!(emp && 'role' in emp && 'department' in emp);
}

export function isExpandedEmployeeWithTickets(emp: Employee | ExpandedEmployee): emp is ExpandedEmployeeWithTickets {
  return !!(emp && 'tickets' in emp && Array.isArray(emp.tickets));
}